<template>
    <div id="back">
    <div id="table">
      <div id="top">
        <span>{{classFullName}}共{{classSize}}人</span>
         <div id="buttonDiv">
          <button class="am-btn am-btn-success am-radius" @click="save()">保存</button>
          <button class="am-btn am-btn-success am-radius">导出</button>
         </div>
      </div>

      <div id="bottom">
        <span>班级名单</span>
      </div>
      <div id="show">
        <table class="operationTable">
          <thead>
            <tr>
              <th>姓名</th>
              <th>学号</th>
              <th>班级任职</th>
              <th>状态</th>
              <th>备注</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="data in tableList">
              <td  v-text="data.studentName"></td>
              <td  v-text="data.studentId"></td>
              <td><select v-model="data.studentDuty">
                <option  v-for="studentDuty in studentDutys" >
                  {{ studentDuty }}
                </option>
              </select></td>
              <td><select  v-model="data.currentState">
                <option  v-for="currentState in currentStates">
                  {{ currentState }}
                </option>
              </select></td>
              <td>
                <textarea v-model="data.changeReason"></textarea>
              </td>
              <td><span>查看学生信息</span></td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: '',
        data () {
            return {
              classFullName:'',
              classSize:'',
              tableList:[{name:'李华',num:'1234'},{name:'王明',num:'1235'},{name:'李萌萌',num:'1236'}],
              studentDutys:[
                '班长',
                '副班长',
                '学习委员',
                '纪律委员',
                '无'
              ],
              currentStates:[
                '在读',
                '停课',
                '休学',
                '其他'
              ]
            }
        },
      beforeMount:function(){
        this.$http.post('../jsonphp/class.php',{},
          {"Content-Type":"application/json"}).then(function (response) {
            console.log(response);
            this.classFullName=response.body.classFullName;
            this.classSize=response.body.classSize;
            this.tableList = response.body.studentAndStateList;
          },
          function(error){
            console.log("获取error:");
            console.log(error);
          });
      },
      methods: {
        save:function(){
          this.$http.post('../jsonphp/class.php',{
            "studentAndStateList":this.tableList
          },{"Content-Type":"application/json"}).then(function (response) {
              console.log("传递:");
              console.log(response.body);
              if(response.body.result=="1")
              {alert("success!")}
            },
            function(error){
              console.log("传递error:");
              console.log(error);
            });
        }
      }
    }
</script>

<style lang="css" scoped>
  @import '../../../../assets/css/external.css';
  #back{
    background-color: #f3f3f3;
  }
#table{
  background-color: white;
  margin-right:5rem;
  margin-left:5rem;
  height:30rem;
  position: relative;
  top:2rem;
}
#top{
  position: relative;
  display:flex;
  justify-content: space-between;
  padding:0.5rem 2rem;
}
#bottom{
    background-color: #158064;
    color:white;
    text-align: left;
    position: relative;
    padding-left:2rem;
   padding-top:0.7rem;
  padding-bottom: 0.7rem;

  }
  textarea{
    border: 0.1rem solid #d4d4d9;
  }
  button{

  }
  /*table{*/
    /*width:96%;*/
    /*margin-left:1rem;*/
    /*margin-right:1rem;*/
    /*border: solid 1px white;*/
    /*border-collapse: collapse;*/
  /*}*/
/*thead{*/
  /*border-bottom:solid 2px lightgrey;*/
/*}*/

tr{
  height:3rem;
}
select{
  border: 0.1rem solid #d4d4d9;
  border-radius: 0.7rem;
  outline: none;
  padding: 0.3rem 0.5rem;
  font-size: 0.8rem;
}
</style>



